export default {
    mixins: [mixin],
    components: {
        'v-footer': Footer
    },
    data() {
        return {
            lists: [],
            prizes: [],
            opentype: 1,
            maskShow: false,
            opentypeShow: false,
            multiArray: multiArray,
            multiIndex: multiIndex,
            isSenior: false,
            content: '',
            iscontent: true,
            qrcodeTips: '',
            qrcode: '',
            qrcodename: '',
            maskShow: false,
            opentypeShow: false,
            qrcodeShow: false,
            qrcodetype: 1,
            passwordShow: false,
            tipsTitle: '',
            tipsText: '',
            tipsBtn: '',
            tipsShow: false,
            copytext: '',
            content: '',
            imageShow: false,
            saveText: '发起抽奖'
        }
    },
    computed: {
        open_type_tips() {
            let _this = this;
            if (_this.opentype == 1) {
                return '到达设定时间自动开奖';
            } else if (_this.opentype == 2) {
                return '到达设定人数自动开奖';
            } else {
                return '发起人手动开奖';
            }
        },
        qrcode_tips() {
            let _this = this;
            if (_this.qrcodetype == 1) {
                return '微信号';
            } else if (_this.qrcodetype == 2) {
                return '公众号';
            } else if (_this.qrcodetype == 3) {
                return '小程序';
            } else {
                return '其他';
            }
        }
    },
    methods: {
        async init() {
            let _this = this;
            console.log(_this.publish)
            _this.prizes.map((value, key, array) => {
                let thumb_key = 'thumb' + key;
                _this.$set(value, 'thumb', _this.publish[thumb_key]);
            });
        },
        addLotteryImg(index) {
            let _this = this;
            uni.chooseImage({
                count: 1,
                sizeType: ["original", "compressed"],
                sourceType: ["album", "camera"],
                success: function(res) {
                    let path = res.tempFilePaths[0];
                    uni.navigateTo({
                        url: "/pages/lottery/cutInside?type=thumb" + index + "&src=" + path
                    });
                }
            });
        },
        delPrize(index) {
            let _this = this;
            let publish = JSON.parse(JSON.stringify(_this.publish));
            _this.$set(publish, ('thumb' + index), '');
            _this.$store.commit('set_publish', publish);
            _this.prizes.splice(index, 1);
        },
        addPrize() {
            let _this = this;
            _this.prizes.push({
                title: '',
                thumb: '../../static/images/cover.png',
                count: ''
            });
        },
        showOpentype() {
            let _this = this;
            _this.opentypeShow = true;
        },
        setOpenType(type) {
            let _this = this;
            _this.opentype = type;
            _this.close();
        },
        timeChange: function(e) {
            let _this = this;
            console.log(e);
            _this.multiIndex = e.mp.detail.value;
        },
        seniorChange(e) {
            let _this = this;
            if (e.mp.detail.value) {
                _this.isSenior = true
            } else {
                _this.isSenior = false
            }
        },
        shareChange(e) {
            let _this = this;
            if (e.mp.detail.value) {
                _this.isShare = true
            } else {
                _this.isShare = false
            }
        },
        close() {
            let _this = this;
            _this.maskShow = false;
            _this.opentypeShow = false;
            _this.qrcodeShow = false;
            _this.tipsShow = false;
            _this.imageShow = false;
        },
        showOpentype() {
            let _this = this;
            _this.maskShow = true;
            _this.opentypeShow = true;
        },
        showQrcode() {
            let _this = this;
            _this.maskShow = true;
            _this.qrcodeShow = true;
        },
        async addQrcode() {
            let _this = this;
            const tempFilePaths = await Utils.chooseImage(1);
            const pictures = tempFilePaths.map(item => {
                return {
                    url: item
                }
            });
            Utils.loading();
            for (let key in pictures) {
                let result = await Utils.uploader(pictures[key].url);
                console.log(result);
                if (result.status == 1) {
                    _this.qrcode = result.data.url;
                }
            }
            Utils.loaded();
        },
        deleteQrcode() {
            let _this = this;
            _this.qrcode = '';
        },
        setQrcodeType(type) {
            let _this = this;
            _this.qrcodetype = type;
        },
        showPassword() {
            let _this = this;
            _this.passwordShow = !_this.passwordShow;
        },
        showPasswordTips() {
            let _this = this;
            _this.maskShow = true;
            _this.tipsTitle = '';
            _this.tipsText = '需输入口令才能参与抽奖，可使用关注公众号或加好友的方式获取口令';
            _this.tipsBtn = '我知道了';
            _this.tipsShow = true;
        },
        showShareTips() {
            let _this = this;
            _this.maskShow = true;
            _this.tipsTitle = '';
            _this.tipsText = '打开此功能后，每成功邀请一位好友参加抽奖，将获得一个抽奖码';
            _this.tipsBtn = '我知道了';
            _this.tipsShow = true;
        },
        setClipboardData() {
            let _this = this;
            uni.setClipboardData({
                data: _this.copytext,
                success: function() {
                    Utils.toast('复制成功');
                }
            });
        },
        save() {
            let _this = this;
            if (_this.opentype == 1) {
                _this.tipsTitle = "你选择了按人数开奖：";
                _this.tipsText = "如果72个小时内未达到开奖人数，将会自动开奖";
                _this.tipsBtn = '我知道了，发起抽奖';
                _this.maskShow = true;
                _this.tipsShow = true;
            }
            if (_this.opentype == 2) {
                _this.tipsTitle = "你选择了手动开奖：";
                _this.tipsText = "如果72小时内未手动开奖，将会由系统自动开奖。";
                _this.tipsBtn = '我知道了，发起抽奖';
                _this.maskShow = true;
                _this.tipsShow = true;
            }
        }
    },
    onShow() {
        let _this = this;
        console.log('onShow');
        if (!_this.shouldLogin) {
            console.log('init');
            _this.init();
        }
    },
}
